import { Component, OnInit } from '@angular/core';
import { TranslateService } from '@ngx-translate/core';
import { TranslateConfigService } from 'src/app/services/translate-config.service';
import { ConfigProvider } from 'src/providers/utils/config';
import { UtilService } from 'src/providers/utils/utils.service';

@Component({
  selector: 'app-plan-agent-details',
  templateUrl: './plan-agent-details.page.html',
  styleUrls: ['./plan-agent-details.page.scss'],
})
export class PlanAgentDetailsPage {
  selectedLanguage:string;
  policyData : any = [];
  agentData:any = [];
  errorMsg :any ;
  planType:any;
  policyNo:any;
  planName :any;
  constructor(
    private translateConfigService: TranslateConfigService,
    public translate:TranslateService,
    public utils : UtilService,
    private config: ConfigProvider

  ) {
    this.selectedLanguage = this.translateConfigService.getDefaultLanguage();
      this.translateConfigService.setLanguage('en');

      this.policyData=  history.state.policyDetailsData;
      this.agentData = this.policyData['agentInfo'];
      this.planType=   history.state.planType;
      this.policyNo=  history.state.policyNo;
      this.planName = this.policyData.planDescription.plan;
      this.errorMsg = this.translate.instant('generic_msgs.no_data');
   }



}
